/*NAV-BAR*/
.nav-bar{
    width: 100%;
    min-height: 60px;
    background: #5896e6;
    position: relative;
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
}
.nav-bar #menu{
    margin: 0 120px 0 10px;
}
.nav-bar #user-name{
    margin: 0 150px 0 10px;
    position: relative;
}
.nav-bar #user-name::before{
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(-25px, -50%);
    width: 20px;
    height: 20px;
    background: red;
    border-radius: 100%;
}
.nav-bar #sales-report{
    margin: 0 120px 0 150px;
}

/* OPTIONS MENU */
.options{
    display: flex;
    justify-content: space-around;
    margin: 40px auto;
}

.chart-size{
    width: 100%;
    height: 500px;
}
.text{
    margin: 30px 0;
    text-align: center;
}
.text h2{
    font-size: 20px;
}